<template>
  <div>
    <h1>{{id?'编辑':'新建'}}管理员</h1>
    <el-form label-width="120px" @submit.native.prevent="save">
      <el-form-item label="用户名">
        <el-input v-model="model.username"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input type="password" v-model="model.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template> 

<script>
export default {
  props: {
    id: {}
  },
  data() {
    return {
      model: {},
    };
  },


  methods: {
    async save() {
      if (this.id) {
        await this.$http.put(`common/admin_user/${this.id}`, this.model);
      } else {
        await this.$http.post("common/admin_user", this.model);
      }

      this.$router.push("/admin_user/list");
      this.$message({
        type: "success",
        message: "保存成功"
      });
    },

    // 获取用户
    async fetch() {
      const res = await this.$http.get(`common/admin_user/${this.id}`);
      this.model = res.data;
    },

  },
  created() {
    // 若存在 id 则使用 fetch 获取分类名
    this.id && this.fetch();
  }
};
</script>

